<template>
  <div class="app-cartinfo">
    <div class="tip mdui-m-t-1">
      <router-link to="/home" class="tip-link">
        <span v-if="allprice.priceSpread>0">{{lang('再买')}}{{money}}{{allprice.priceSpread}}{{lang('免运费')}}</span>
		<span  v-else>{{lang('现在下单，立享包邮！')}}</span>
        <span>
          {{lang('再逛逛')}}
          <i class="mdui-icon material-icons array-right">keyboard_arrow_right</i>
        </span>
      </router-link>
    </div>
    <div class="cart-content">
      <div class="product-info" v-for="(item,index) of cart.list" :key="index">
        <div style="width: 100%">
          <label class="mdui-checkbox">
            <input type="checkbox" @click="checked_one(index)" :checked="item.checked">
            <i class="mdui-checkbox-icon"></i>
          </label>
          <router-link :to="'/detail/'+item.tid+'/'+item.classgoods_id" class="goBuy">
            <img :src="item.img?config.cdnbase+item.img:''" width="80" height="80">
          </router-link>
          <div class="info">
            <div class="active-sign">
              <span class="sign">{{lang('限时')}}</span>
              <span class="name">{{item.name}}</span>
            </div>
            <div class="specifications">
              {{item.specname}}
              <em v-if="item.option1">/</em>
              {{item.option1}}
              <em v-if="item.option2">/</em>
              {{item.option2}}
            </div>
            <div class="price-count">
              <span class="price">
                {{item.money}}{{item.price}}
                <span
                  class="original-price"
                >{{item.money}}{{item.oldprice}}</span>
              </span>
              <div class="right-edit">
                <img src="../../assets/image/icon-del.png" width="14" height="14" @click="deletePro(index)">
                <div class="count">
                  <button class="remove" @click="decNum(item,index)">
                    <i class="mdui-icon material-icons">remove</i>
                  </button>
                  <input
                          class="mdui-numbox-input"
                          type="number"
                          :value="item.number"
                          readonly
                          onfocus="this.blur()"
                  >
                  <button class="add" @click="addNum(item,index)">
                    <i class="mdui-icon material-icons">add</i>
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="mdui-bottom-nav mdui-row mdui-color-white" style="bottom: 50px;">
        <div class="selectAll">
          <label class="mdui-checkbox">
            <input type="checkbox" checked @click="checkAll" v-model="checkall">
            <i class="mdui-checkbox-icon"></i>
            {{lang('全选')}}
          </label>
        </div>
        <!--<div class="info"><span><i>{{money}}</i>{{allprice}}</span><span>{{lang('全选')}}活動優惠:$13</span></div>-->
        <button
          id="buyNow"
          class="mdui-btn mdui-btn-block mdui-color-red-900"
          :class="checked.length>0?'':'no-active'"
          @click="sureOrder"
          :disabled="checked.length===0?'disabled':false"
        >
          <span class="mdui-date" v-show="addtime_flag">{{cart_m}}:{{cart_s}}</span>
          {{lang('确认下单')}}
        </button>
      </div>
    </div>
    <div class="bg-modle" v-show="deleteShow" @click="deleteShow=false"></div>
    <div class="mdui-dialog delete-confirm mdui-dialog-open" style="display: block;top: 30%; height: 240px;" v-if="deleteShow">
      <div class="mdui-dialog-title">{{lang('确定要删除吗？')}}</div>
      <div class="mdui-dialog-content" style="height: 124px;">
        <div style="width: 80px;display: inline-block">
          <img class="mdui-cart-product-sku-img" :src="config.cdnbase+cart.list[deleteIndex].img" >
        </div>
        <div class="mdui-cart-product-sku-info">
          <div class="mdui-list-item-title mdui-list-item-one-line">{{cart.list[deleteIndex].name}}</div>
          <div class="mdui-list-item-text mdui-list-item-one-line">
            {{cart.list[deleteIndex].specname}}
            <em v-if="cart.list[deleteIndex].option1">/</em>
            {{cart.list[deleteIndex].option1}}
            <em v-if="cart.list[deleteIndex].option2">/</em>
            {{cart.list[deleteIndex].option2}}</div>
        </div>
      </div>
      <div class="mdui-dialog-actions ">
        <a href="javascript:void(0)" class="mdui-btn mdui-ripple mdui-text-color-primary" @click="sureDelete">{{lang('确认')}}</a>
        <a href="javascript:void(0)" class="mdui-btn mdui-ripple mdui-text-color-primary" @click="deleteShow=false">{{lang('取消')}}</a>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { cartCheck } from '../../lib/goods'
export default {
  data() {
    return {
      list: [], // 推荐列表
      cart: [],
      money: "",
      checkall: false,
      checked: [],
      addtime_flag: false,
      cart_m: 0,
      cart_s: 0,
      deleteShow: false,
      deleteIndex: -1, // 要删除商品的下标
      overdue: [] //商品是否过期
    };
  },
  async mounted() {
    if (this.cart && this.cart.list.length !== 0) {
      let data = []
      for (let item of this.cart.list) {
        data.push({ goods_id: item.goods_id, id: item.sku ,tid: item.tid,cid:item.classgoods_id})
      }
      this.overdue = await cartCheck.checkResult(data)
      for (let i=0; i<this.overdue.length; i++){
        if (this.overdue[i] === 0){
          this.cart.list.splice(i, 1);
          this.overdue.splice(i, 1);
          i--
        }
      }
      if (this.cart.list.length === 0) {
        this.$emit('hasCart')
      }
      localStorage.setItem('cart_info',JSON.stringify(this.cart))
    }
    this.check_state();
  },
  methods: {
    // 单选
    checked_one(index) {
      if (this.cart.list[index].checked) {
        this.cart.list[index].checked = false;
        let arr_index = this.checked.indexOf(this.cart.list[index].id);
        if (arr_index > -1) {
          this.checked.splice(arr_index, 1);
        }
      } else {
        this.cart.list[index].checked = true;
        this.checked.push(this.cart.list[index].id);
      }
      if (this.checked.length === this.cart.list.length) {
        this.checkall = true;
      } else {
        this.checkall = false;
      }
      localStorage.setItem("cart_info", JSON.stringify(this.cart));
    },
    // 全选
    checkAll: function() {
      var _this = this;
      if (this.checkall) {
        // 实现反选
        this.checked = [];
        this.cart.list.forEach(function(item) {
          item.checked = false;
        });
      } else {
        // 实现全选
        this.checked = [];
        this.cart.list.forEach(function(item) {
          item.checked = true;
          _this.checked.push(item.id);
        });
      }
      if (this.checked.length === this.cart.list.length) {
        this.checkall = true;
      }
    },
    // 判断当前购物是否是全选状态
    check_state() {
      this.cart.list.forEach(item => {
        if (item.checked) {
          this.checked.push(item.id);
        }
      });
      if (this.checked.length === this.cart.list.length) {
        this.checkall = true;
      }
    },
    // 数量加减
    decNum(item, index) {
      if (item.number > 1) {
        item.number -= 1;
        let num = item.number;
        if (this.cart.gift.length > 0) {
          for (let i = 0; i < this.cart.gift.length; i++) {
            this.cart.gift[i][index].number = num;
          }
        }
        localStorage.setItem("cart_info", JSON.stringify(this.cart));
      } else {
        item.number = 1;
      }
    },
    addNum(item, index) {
      item.number += 1;
      let num = item.number;
      if (this.cart.gift.length > 0) {
        for (let i = 0; i < this.cart.gift.length; i++) {
          this.cart.gift[i][index].number = num;
        }
      }
      localStorage.setItem("cart_info", JSON.stringify(this.cart));
    },
    sureOrder() {
      this.$router.push("/orderConfirm");
    },
    //倒计时
    cart_GetRTime() {
      let EndTime = new Date(this.cart.add_time);
      EndTime.setDate(EndTime.getDate());
      let NowTime = new Date();
      EndTime.setDate(EndTime.getDate());
      let t = EndTime.getTime() - NowTime.getTime();
      if (t <= 0 || !t) {
        this.addtime_flag = false;
      } else {
        this.cart_m = 0;
        this.cart_s = 0;
        if (t >= 0) {
          this.cart_m = Math.floor((t / 1000 / 60) % 60);
          if (this.cart_m < 10) {
            this.cart_m = "0" + Math.floor((t / 1000 / 60) % 60);
          }
          this.cart_s = Math.floor((t / 1000) % 60);
          if (this.cart_s < 10) {
            this.cart_s = "0" + Math.floor((t / 1000) % 60);
          }
        }
        this.addtime_flag = true;
      }
    },
    deletePro (i) {
      this.deleteIndex = i
      this.deleteShow = true
    },
    sureDelete () {
      this.cart.list.splice(this.deleteIndex, 1)
      this.deleteShow = false
      localStorage.setItem('cart_info',JSON.stringify(this.cart))
      if (this.cart.list.length === 0) {
        this.$emit('hasCart')
      }
    }
  },
  created() {
    let data = JSON.parse(localStorage.getItem("cart_info"));
    this.cart = data;
    this.money = data.money;
    setInterval(this.cart_GetRTime, 300);
    if (this.cart_m === 0 && this.cart_s === 0) {
      clearInterval(this.cart_GetRTime);
    }
  },
  computed: {
    ...mapState(["siteInfo", "classInfo", "language", "config"]),
    lang() {
      return function(src) {
        return this.language ? this.language[src] : "";
      };
    },
    allprice() {
      let allprice = 0;
      let priceSpread = 0;
      if (this.cart) {
        for (let item of this.cart.list) {
          if (item.checked) {
            allprice += item.number * item.price;
          }
        }
      }
      if(this.siteInfo.free_post_price>0){
        priceSpread = this.siteInfo.free_post_price - allprice
      }
      return {allprice,priceSpread};
    }
  },
  watch: {
    "cart.list": function() {
      localStorage.setItem("cart_info", JSON.stringify(this.cart));
    },
    deep: true
  }
};
</script>

