<template>
  <div class="cart">
    <div v-show="!isShow">
      <header-top class="header-fixed" :kfshow.sync="kfshow">
        <img
          width="28"
          height="28"
          src="../../assets/image/icon-menu.png"
          alt="menu"
          class="icon-menu"
          slot="menu"
          @click="showMenu"
        >
        <div class="mdui-center mdui-valign point-progress" slot="title">
          <ul>
            <li class="active">
              <i>1</i>
            </li>
            <li></li>
            <li>
              <i>2</i>
            </li>
            <li></li>
            <li>
              <i>3</i>
            </li>
          </ul>
          <ul>
            <li class="active">
              <i>{{lang('确认商品')}}</i>
            </li>
            <li>
              <i>{{lang('填写付款资料')}}</i>
            </li>
            <li>
              <i>{{lang('订单完成')}}</i>
            </li>
          </ul>
        </div>
        <img
          width="28"
          height="28"
          src="../../assets/image/logo.png"
          alt="logo"
          class="logo"
          slot="home"
          @click="kfShow()"
        >
      </header-top>
      <div style="height: 40px;"></div>
      <no-product v-if="!shopping_carshow"/>
      <cart-info v-else @hasCart="shopping_carshow=false"/>
      <footer-nav></footer-nav>
    </div>
    <class-list v-if="isShow" :is-show="isShow" @showMenu="isShow=false" :first-list="firstLists"/>
  </div>
</template>
<script>
import cartInfo from "./CartInfo.vue";
import noProduct from "./Noproduct.vue";
import { mapState } from "vuex";
export default {
  data() {
    return {
      kfshow: false,
      // 控制显示是否存在商品页面
      shopping_carshow: false,
      isShow: false,
      type: 1,
      firstLists: ""
    };
  },
  methods: {
    kfShow() {
      this.kfshow = !this.kfshow;
    },
    showMenu() {
      this.isShow = true;
    }
  },
  components: {
    cartInfo,
    noProduct
  },
  async created() {
    this.firstLists = this.classInfo;
    let cart_info = localStorage.getItem("cart_info");
    if (!cart_info) {
      this.shopping_carshow = false;
    } else {
      cart_info = JSON.parse(cart_info);
      if (cart_info.list.length === 0) {
        this.shopping_carshow = false;
      } else {
        this.shopping_carshow = true;
      }
    }
  },
  computed: {
    ...mapState(["classInfo", "language"]),
    lang() {
      return function(str) {
        return this.language ? this.language[str] : "";
      };
    }
  }
};
</script>
<style scoped lang="stylus">
.mdui-checkbox input[type=checkbox]:focus:checked:not(:disabled)+.mdui-checkbox-icon, .mdui-checkbox:active input[type=checkbox]:checked:not(:disabled)+.mdui-checkbox-icon
  -webkit-box-shadow: none
  -moz-box-shadow: none
  box-shadow: none
.mdui-checkbox input[type=checkbox]:focus+.mdui-checkbox-icon, .mdui-checkbox:active input[type=checkbox]+.mdui-checkbox-icon
  -webkit-box-shadow: none
  -moz-box-shadow: none
  box-shadow: none
.cart
  .point-progress
    flex-direction: column;
    width: 56%;
    padding-bottom: 3px;
    padding-left: 0
    display: flex
    &>*
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    ul
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      li
        i
          transform: scale(.8);
          text-align: center;
          margin: 0 auto;
          display: block;
          font-size: 0;
          color: #000;
          font-style: normal;
          line-height: 16px;
        &.active
          i
            color: #fff;
            font-size: 12px;
    ul:first-child
      width: calc(100% - 36px);
      li:nth-child(2n+1)
        border-radius: 50%;
        background: #F8E9EA;
        height: 10px;
        width: 10px;
        &.active
          height: 16px;
          width: 16px;
          background: #B4272D;
      li:nth-child(2n)
        border-bottom: 3px solid #F8E9EA;
        flex: 1;
        &.active
          border-color: #B4272D;
    ul:last-child
      li
        i
          font-size: 12px;
          color: #777;
        &.active i
          color: #000;
  .header-fixed
    position: fixed
    top: 0
    left: 0
    right: 0
    z-index 100
    background-color: #fff;
    border-bottom: 1px solid #ededed
    margin: 0 auto
    max-width: 768px
</style>
